"use client";

import { useState } from "react";
import BeforeAfterSliderRange from "@/components/BeforeAfterSliderRange";
import { ChevronLeft, ChevronRight, Quote } from "lucide-react";

export default function BeforeAfterCard({ data }) {
  const [index, setIndex] = useState(0);
  const [position, setPosition] = useState(50);

  const nextSlide = () => {
    setIndex((i) => {
      const v = (i + 1) % data.length;
      setPosition(50); // reset slider position on change (like Dr Batra)
      return v;
    });
  };
  const prevSlide = () => {
    setIndex((i) => {
      const v = (i - 1 + data.length) % data.length;
      setPosition(50);
      return v;
    });
  };

  const t = data[index];

  return (
    <div className="w-full bg-background-soft rounded-3xl p-6 md:p-10 shadow-soft border border-border">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-10 items-stretch">

        {/* LEFT — Dr Batra style slider */}
        <div>
          <BeforeAfterSliderRange
            before={t.before}
            after={t.after}
            value={position}
            onChange={(v) => setPosition(v)}
          />

          <p className="text-center text-xs text-muted-foreground mt-2">
            *Real patient images
          </p>
        </div>

        {/* RIGHT — Patient Details */}
        <div className="bg-card shadow-medium border border-border rounded-3xl p-6 relative flex flex-col">
          <Quote className="absolute top-5 left-5 w-10 h-10 text-muted-foreground/20" />

          <div className="space-y-3 mt-10 text-sm leading-relaxed text-foreground flex-1">
            <p>
              <strong>Patient Name – </strong> {t.name}
            </p>
            <p>
              <strong>Treatment Name – </strong> {t.treatment}
            </p>
            <p>
              <strong>Duration – </strong>
              <span className="font-semibold">{t.duration}</span>
            </p>
            <p>
              <strong>Clinic – </strong> {t.clinic}
            </p>

            <p className="text-foreground">
              {t.feedback}
              <span className="text-primary cursor-pointer">...read more</span>
            </p>
          </div>

          {/* Navigation arrows (bottom-left) */}
          <div className="flex gap-3 mt-6">
            <button
              onClick={prevSlide}
              aria-label="Previous patient"
              className="w-10 h-10 rounded-full border border-primary text-primary flex items-center justify-center hover:bg-primary/10 transition"
            >
              <ChevronLeft className="w-5 h-5" />
            </button>

            <button
              onClick={nextSlide}
              aria-label="Next patient"
              className="w-10 h-10 rounded-full border border-primary text-primary flex items-center justify-center hover:bg-primary/10 transition"
            >
              <ChevronRight className="w-5 h-5" />
            </button>
          </div>
        </div>

      </div>
    </div>
  );
}
